<template>
  <transition name="fade">
    <div class="chat" :style="'backgroundImage:url('+getchatbg+')'">
        <div class="top">
          <mt-header :title="getonlinecount" class="top1">
                <router-link to="#" slot="left">
                   <mt-button icon="back" @click="back"></mt-button>
                </router-link>
                <mt-button icon="more" slot="right"></mt-button>
          </mt-header>
        </div>
        <div class="room">
          <div v-for="obj in getmesshistoryinfos">
            <mymsg v-if="obj.username==getusername" :name="obj.username" :head="obj.src" :msg="obj.msg"
                    :img="obj.img" ></mymsg>
            <othermsg v-if="obj.username!=getusername" :name="obj.username" :head="obj.src" :msg="obj.msg"
                 :img="obj.img" ></othermsg>
          </div>
          <div v-for="obj in getinfos">
          <mymsg v-if="obj.username==getusername" :name="obj.username" :head="obj.src" :msg="obj.msg"
               :img="obj.img" ></mymsg>
          <othermsg v-if="obj.username!=getusername" :name="obj.username" :head="obj.src" :msg="obj.msg"
               :img="obj.img" ></othermsg>
          </div>
        </div>
        <div class="inp">
          <div class="text1">
            <div class="emoj"><img src="static/pic/emo1.png" alt="" @click="emo" ></div>
            <div class="upload" :style="'backgroundImage:url('+getuploadimg+')'">
              <input type="file" style="width:100%;height:100%;opacity:0" @change="upload">
            </div>
          </div>
          <emo v-if="showemo"></emo>
          <div class="text">
            <input type="text" class="inp1"  v-model="messg">
            <button type="button" class="btn" @click="submess" ></button>
          </div>
        </div>
    </div>
  </transition>
</template>
<style lang="stylus" rel="stylesheet/stylus" scoped>
  &.fade-enter-active, &.fade-leave-active
    transition: all 0.2s linear
    transform translate3d(0, 0, 0)

  &.fade-enter, &.fade-leave-active
    opacity: 1
    transform translate3d(100%, 0, 0)

  .chat
    position: fixed
    top: 0
    left: 0
    width: 100%
    flex: 1
    z-index: 99
    height: 100%
    display: flex
    opacity: 1
    flex-direction: column
    .top
       width: 100%
       height: 1.066667rem
       .top1
         height: 1.066667rem
    .room
       height: 14.666667rem
       overflow: auto
    .inp
        width: 100%
        height: 2.053333rem
        position: relative
        background: rgb(241,241,241)
        font-size: 0
        .text1
           .emoj
             width: 0.8rem
             height: 0.8rem
             vertical-align: top
             display: inline-block
             img
                widht: 100%
                height: 100%
           .upload
             margin-left: 0.2rem
             width: 0.8rem
             display: inline-block
             height: 0.8rem
             background-size: 100% 0.8rem
             background-repeat: no-repeat
             input
               width: 100%
               height: 100%
        .text
           display: flex
           .inp1
              font-size: 0.186667rem
              height: 0.986667rem
              flex:7
              vertical-align: top
              border-radius: 0.133333rem
              outline: none
              font-size: 0.373333rem
           .btn
              flex:3
              height: 0.986667rem
              background: #26A2FF
              border-radius: 0.266667rem
              outline: none
              border: none
</style>
<script type="text/ecmascript-6">
  import Exif from 'exif-js'
  import emo from '../emo'
  import mymsg from '../mymsg'
  import othermsg from '../othermsg'
  import io from 'socket.io-client'
  import { MessageBox } from 'mint-ui'
  import {mapGetters} from 'vuex'
  export default {
    data() {
      return {
        messg: "",
        showemo: false
      }
    },
    created() {
      const that = this
      this.socket = io.connect('http://172.17.152.123:3000')
      this.getsocket.on('message', function (obj) {
        that.$store.commit('addroomdetailinfos', obj)
        window.scrollTo(0, 0)
      })
      this.getsocket.on('logout', function (obj) {
        that.$store.commit('setonlinecount', Object.keys(obj).length);
      })
      this.getsocket.on('disconnect', function () {

      })
    },
    methods: {
      submess() {
        if (this.messg != "") {
          var data = {
            username: this.getusername,
            msg: this.messg,
            src: this.getusersrc,
            img: '',
            room: this.getuserroom
          }
          this.getsocket.emit('message', data);
          this.messg = '';
        } else {
          MessageBox('', '');
        }
      },
      back() {
        this.$store.commit('changeshowchat');
        var obj = {
          name: this.getusername,
          roomid: this.getuserroom
        }
        this.getsocket.emit('logout', obj)
        this.getsocket.emit('disconnect', function () {
        })
      },
      emo() {
        this.showemo = !this.showemo;
      },

      upload(e) {
        let files = e.target.files || e.dataTransfer.files;
        if (!files.length) return;
        this.picValue = files[0];
        this.imgPreview(this.picValue);
      },
      imgPreview(file) {
        let self = this;
        let Orientation;
        //
        Exif.getData(file, function () {
          Orientation = Exif.getTag(this, 'Orientation');
        });
        // FileReader
        if (!file || !window.FileReader) return;
        if (/^image/.test(file.type)) {
          // reader
          let reader = new FileReader();
          // 2 base64 
          reader.readAsDataURL(file);
          // 
          reader.onloadend = async function () {
            let result = this.result;
            let img = new Image();
            img.src = result;
            //100K,
            if (this.result.length <= (100 * 1024)) {
              self.headerImage = this.result;
              self.postImg();
            } else {
              img.onload = function () {
                let data = self.compress(img, Orientation);
                self.headerImage = data;
                self.postImg();
              }
            }
          }
        }
      },
      postImg() {
        var data = {
          username: this.getusername,
          msg: '',
          src: this.getusersrc,
          img: this.headerImage,
          room: this.getuserroom
        }
        this.getsocket.emit('message', data);
      },
    rotateImg(img, direction, canvas) {
      //4
      const min_step = 0;
      const max_step = 3;
      if (img == null) return;
      //imgimg
      let height = img.height;
      let width = img.width;
      let step = 2;
      if (step == null) {
        step = min_step;
      }
      if (direction == 'right') {
        step++;
        //
        step > max_step && (step = min_step);
      } else {
        step--;
        step < min_step && (step = max_step);
      }
      //
      let degree = step * 90 * Math.PI / 180;
      let ctx = canvas.getContext('2d');
      switch (step) {
        case 0:
          canvas.width = width;
          canvas.height = height;
          ctx.drawImage(img, 0, 0);
          break;
        case 1:
          canvas.width = height;
          canvas.height = width;
          ctx.rotate(degree);
          ctx.drawImage(img, 0, -height);
          break;
        case 2:
          canvas.width = width;
          canvas.height = height;
          ctx.rotate(degree);
          ctx.drawImage(img, -width, -height);
          break;
        case 3:
          canvas.width = height;
          canvas.height = width;
          ctx.rotate(degree);
          ctx.drawImage(img, -width, 0);
          break;
      }
    },
    compress(img, Orientation) {
      let canvas = document.createElement("canvas");
      let ctx = canvas.getContext('2d');
      //canvas
      let tCanvas = document.createElement("canvas");
      let tctx = tCanvas.getContext("2d");
      let initSize = img.src.length;
      let width = img.width;
      let height = img.height;
      //400
      let ratio;
      if ((ratio = width * height / 4000000) > 1) {
        console.log("400")
        ratio = Math.sqrt(ratio);
        width /= ratio;
        height /= ratio;
      } else {
        ratio = 1;
      }
      canvas.width = width;
      canvas.height = height;
      // 
      ctx.fillStyle = "#fff";
      ctx.fillRect(0, 0, canvas.width, canvas.height);
      //100
      let count;
      if ((count = width * height / 1000000) > 1) {
        console.log("100W");
        count = ~~(Math.sqrt(count) + 1); //
        //  
        let nw = ~~(width / count);
        let nh = ~~(height / count);
        tCanvas.width = nw;
        tCanvas.height = nh;
        for (let i = 0; i < count; i++) {
          for (let j = 0; j < count; j++) {
            tctx.drawImage(img, i * nw * ratio, j * nh * ratio, nw * ratio, nh * ratio, 0, 0, nw, nh);
            ctx.drawImage(tCanvas, i * nw, j * nh, nw, nh);
          }
        }
      } else {
        ctx.drawImage(img, 0, 0, width, height);
      }
      //ios 
      if (Orientation != "" && Orientation != 1) {
        switch (Orientation) {
          case 6://90
            this.rotateImg(img, 'left', canvas);
            break;
          case 8://90
            this.rotateImg(img, 'right', canvas);
            break;
          case 3://180
            this.rotateImg(img, 'right', canvas);//
            this.rotateImg(img, 'right', canvas);
            break;
        }
      }
      //
      let ndata = canvas.toDataURL('image/jpeg', 0.1);
      console.log('' + initSize);
      console.log('' + ndata.length);
      console.log('' + ~~(100 * (initSize - ndata.length) / initSize) + "%");
      tCanvas.width = tCanvas.height = canvas.width = canvas.height = 0;
      return ndata;
    }
          },
      computed : {
        ...mapGetters([
            'getsocket',
            'getmesshistoryinfos',
            'getuserroom',
            'getusersrc',
            'getusername',
            'getinfos',
            'getonlinecount',
            'getuploadimg',
            'getchatbg'
        ])
      },
      components : {
        mymsg,
        othermsg,
        emo
      }
  }
</script>



