<template>
<transition name="fade">
   <div class="robotchat">
        <div class="top">
          <mt-header :title="getonlinecount" class="top1">
                <router-link to="#" slot="left">
                   <mt-button icon="back" @click="back"></mt-button>
                </router-link>
                <mt-button icon="more" slot="right"></mt-button>
          </mt-header>
        </div>
        <div class="middle">
         <div v-for="obj in getrobotmsg">
           <othermsg v-if="obj.name!=getusername" :head="obj.src" :msg="obj.message"
            :img="obj.img"></othermsg>
           <mymsg v-if="obj.name==getusername"  :head="obj.src" :msg="obj.message"
            :img="obj.img"></mymsg>
         </div>
        </div>
        <div class="inputtext">
          <input type="text" v-model.trim="robotmess">
          <button type="button" @click="subrobotmess"></button>
        </div>
      </div>
 </transition>
</template>
<style lang="stylus" rel="stylesheet/stylus" scoped>
&.fade-enter-active, &.fade-leave-active
    transition: all 0.2s linear
    transform translate3d(0, 0, 0)

  &.fade-enter, &.fade-leave-active
    opacity: 1
    transform translate3d(100%, 0, 0)
.robotchat
       position: fixed
       left: 0
       top: 0
       width: 100%
       height: 100%
       overflow: auto
       z-index: 99
       background-color: white
       .top
         height: 1.066667rem
         .top1
            height: 1.066667rem
       .middle
          width: 100%
          height: 15.2rem
          overflow: auto
       .inputtext
          width: 100%
          height: 1.52rem
          text-align: center
          display: flex
          background: #F1F1F1
          input
            flex: 0 0 69.5%
            font-size: 0.373333rem
            margin-top: 0.226667rem
            height: 1.066667rem
            vertical-align: top
            border-radius: 0.133333rem
            outline: none
            border: 0.013333rem solid gray
          button
            flex: 0 0 30%
            margin-top: 0.226667rem
            background: #26A2FF
            height: 1.066667rem
            outline: none
            border: none
            color: white
            border-radius: 0.133333rem
</style>
<script type="text/ecmascript-6">
import { MessageBox } from 'mint-ui'
import mymsg from '../mymsg'
import othermsg from '../othermsg'
import {mapGetters} from 'vuex'
  export default {
      data () {
         return {
            robotmess:''
         }
      },
      methods: {
           subrobotmess () {
            if(this.robotmess) {
                 var data = {
                  info : this.robotmess,
                  id : this.getusername
             }
             this.$store.dispatch('getrobotmess',data)
             this.$store.commit('setrobotmsg',{
                 message : this.robotmess,
                 name : this.getusername,
                 src: this.getusersrc
             });
             this.robotmess = '';
            } else {
               MessageBox('', '');
            }
         },
          back () {
              this.$store.commit('changeshowrobotchat');
         }
      },
    computed: {
         ...mapGetters([
              'getmesshistoryinfos',
              'getrobotmsg',
              'getusername',
              'getusersrc',
         ])
    },
    components: {
        mymsg,
        othermsg
    }
  }
</script>


