<template>
   <div class="rating">
      <div class="house common mt" @click="chat('rom1')">
        <img src="static/pic/chat.png" alt="">
        <p class="p1"></p>
        <img src="static/pic/sheng.png" alt="">
      </div>
      <div class="robotroom common" @click="robotchat">
        <img src="static/pic/robot.png" alt="">
        <p class="p1"></p>
        <img src="static/pic/sheng.png" alt="">
      </div>
      <div class="friend common">
       <img src="static/pic/friend.png" alt="">
       <p class="p1"></p>
        <img src="static/pic/sheng.png" alt="">
      </div>
      <chat v-show="getshowchat"></chat>
      <robotchat v-show="getshowrobotchat"></robotchat>
   </div>
</template>
<style lang="stylus" rel="stylesheet/stylus" scoped>
  .rating
    height: 100%
    width: 100%
    padding-top: 1.0rem
    .common
       height: 1.333333rem
       width: 90%
       margin: 0 auto
       border-radius: 0.066667rem
       display: flex
       background: #26A2FF
    .p1
         flex: 1
         display: inline-block
         text-align: center
         line-height: 2.5
         height: 1.333333rem
         font-size: 0.48rem
         color: white
    .house
       img
         display: inline-block
         height: 0.933333rem
         width: 0.933333rem
         flex: 0 0 0.933333rem
         margin-top: 0.2rem
         margin-left: 0.2rem

    .robotroom
       margin-top: 0.8rem
       img
         display: inline-block
         height: 0.933333rem
         width: 0.933333rem
         flex: 0 0 0.933333rem
         margin-top: 0.2rem
         margin-left: 0.2rem
    .friend
      margin-top: 0.8rem
      img
         display: inline-block
         height: 0.933333rem
         width: 0.933333rem
         flex: 0 0 0.933333rem
         margin-top: 0.2rem
         margin-left: 0.2rem

</style>
<script type="text/ecmascript-6" scoped>
  import chat from '../chat/chat'
  import robotchat from '../robotchat/robotchat'
  import io from 'socket.io-client'
  import {mapGetters} from 'vuex'
     export default {
        data () {
          return {
             socket:  "",
             robotmess: "",
             onlinecount: 0
          }
        },
       created (){
           var that  = this;
           this.$store.commit('setsocket',io.connect('http://172.17.152.123:3000'));
           this.getsocket.on('connect',function(){
                 console.log('ok');
           })
           this.getsocket.on('join',function(obj){
                that.$store.commit('setonlinecount',''+Object.keys(obj).length);
           })
         this.getsocket.on('mess',function(data){
                 alert(data);
         })
         this.getsocket.on('login',function(obj){
              console.log(obj);
         });
       },
       computed : {
         ...mapGetters([
           'getsocket',
           'getshowchat',
           'getuserroom',
           'getshowrobotchat',
           'getusername'
         ])
       },
       methods : {
          chat(roomID){
              this.$store.commit('changeshowchat');
              var obj = {
                 name : this.getusername,
                 roomid : roomID,
                 src: this.getusersrc
              }
              this.$store.commit('setuserroom',roomID);
              this.getsocket.emit('join',obj);
              this.getsocket.emit('mail',{to:"dd",msg:"hello"});
              var data = {
                 roomid : roomID
              }
              this.$store.dispatch('getmesshistory',data)
          },
          robotchat () {
             this.$store.commit('changeshowrobotchat')
          }
       },
       components : {
          chat,
          robotchat
       }
     }
</script>



