<template>
  <div class="emo">
    <div v-for="item in emoj" style="display: inline-block" @click="send(item)">
      <img :src="item" alt="" >
    </div>
  </div>
</template>
<style lang="stylus" rel="stylesheet/stylus" scoped>
  .emo
     position: absolute
     top: -30px
     left: 0
     width: 100%
     background: rgb(238,238,238)
     img
       width: 30px
       height: 30px

</style>
<script type="text/ecmascript-6">
   export default {
       data () {
         return {
           emoj:["static/pic/emoji-1.png","static/pic/emoji-2.png","static/pic/emoji-3.png","static/pic/emoji-4.png","static/pic/emoji-5.png","static/pic/emoji-6.png"]
         }
       }
   }
</script>



